import React, { createContext, useCallback, useState } from "react";
import { Cart, User, CheckoutResult, ShopAPI } from "@portals-ecommerce/shared";

export const DataContext = createContext<{
  loading: boolean;
  cart: Cart | undefined;
  checkout: () => CheckoutResult;
  user: User | undefined;
  setUserData: (user: User) => void;
  setStateData: (opts: { user?: User; cart?: Cart }) => void;
}>({
  loading: false,
  cart: undefined,
  user: undefined,
  checkout: () => {
    throw new Error("Method not implemented");
  },
  setUserData: () => {
    throw new Error("Method not implemented");
  },
  setStateData: () => {
    throw new Error("Method not implemented");
  },
});

export const DataProvider: React.FC = ({ children }) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [user, setUser] = useState<User>();
  const [cart, setCart] = useState<Cart>();

  const setStateData = useCallback((opts: { user?: User; cart?: Cart }) => {
    setLoading(true);
    const { cart, user } = opts;
    user && setUser(user);
    cart && setCart(cart);
    setLoading(false);
  }, []);

  const checkout = (): CheckoutResult => {
    return ShopAPI.checkout();
  };

  const setUserData = (user: User) => {
    setLoading(true);
    setUser(user);
    setLoading(false);
  };

  return (
    <DataContext.Provider
      value={{
        loading,
        user,
        cart,
        checkout,
        setUserData,
        setStateData,
      }}
    >
      {children}
    </DataContext.Provider>
  );
};
